.movieModal {
    position: absolute;
    content: '';
    top: 0;
    left: 0;
    background-color: rgba(0, 0, 0, 0.5);
    backdrop-filter: blur(20px);
    box-shadow: 0 0 40px rgba(255, 255, 255, 0.5);
    z-index: 1500;
    display: flex;
    justify-content: center;
    align-items: center;
    opacity: 0;
    visibility: hidden;
    transition: all ease-in-out 1s;
    width: 100%;
    height: 100%;
}

.movieModal iframe{
    max-width: 1280px;
    max-height: 720px;
}

.movieModal .modalClose{
    position: absolute;
    top: 30px;
    right: 30px;
    cursor: pointer;
    color: #fff;
    font-size: 1.8rem;
}

.movieModal.active {
    opacity: 1;
    visibility: visible;
}